/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

/* Default Global Styles */
h5.feed-title-slideshow-mgarcia span {
    font-size: 80%;
    opacity: 0.6;
    font-style: italic;
}

h5.feed-title-slideshow-mgarcia {
    margin-bottom: 10px;
}
.content-container-slideshow-mgarcia {
    padding: 10px;
}
.date-slideshow-mgarcia,
.dash-slideshow-mgarcia {
    opacity: 0.65;
}
.excerpt-slideshow-mgarcia {
    font-size: 85%;
}

/* Flickity Custom Styles */
.flickity-viewport {
    transition: height 0.2s;
}
.carousel-cell-mgarcia-2 {
    width: 50%;
}
.carousel-cell-mgarcia-3 {
    width:33.3%;
}
.carousel-cell-mgarcia-4 {
    width: 25%;
}


/* Show/Hide Elements */
.feed-title-slideshow-mgarcia,
.excerpt-slideshow-mgarcia,
.meta-list-slideshow-mgarcia,
.wp-block-post-featured-image {
    display: none;
    visibility: hidden;
}
.has-feed-title .feed-title-slideshow-mgarcia,
.has-excerpt .excerpt-slideshow-mgarcia,
.has-meta .meta-list-slideshow-mgarcia,
.has-featured .wp-block-post-featured-image {
    display: block;
    visibility: visible;
}